import { Toolbar, IconButton, Paper, Button, Card, Box, Divider,  } from "@mui/material"
import BoldIcon from '@mui/icons-material/FormatBold';
import ItalicIcon from '@mui/icons-material/FormatItalic';
import UnderlinedIcon from '@mui/icons-material/FormatUnderlined';
import QuoteIcon from '@mui/icons-material/FormatQuote';
import ListNumberedIcon from '@mui/icons-material/FormatListNumbered';
import ListBulletedIcon from '@mui/icons-material/FormatListBulleted';
import AlignLeftIcon from '@mui/icons-material/FormatAlignLeft';
import FormatAlignCenterIcon from '@mui/icons-material/FormatAlignCenter';
import FormatAlignRightIcon from '@mui/icons-material/FormatAlignRight';
import isHotkey from 'is-hotkey'
import FormatAlignJustifyIcon from '@mui/icons-material/FormatAlignJustify';
import ToolButton from "./ToolButton";
import { Editable, Slate, withReact, } from "slate-react";
import { useCallback, useMemo } from "react";
import { createEditor} from "slate";
import Element from "./Element";
import Leaf from "./Leaf";
import {toggleMark} from "./utils";

const HOTKEYS: {[key:string]: string} = {
  'mod+b': 'bold',
  'mod+i': 'italic',
  'mod+u': 'underline',
  'mod+`': 'code',
}

const Editor = () => {
  const renderElement = useCallback((props: any) => <Element {...props} />, [])
  const renderLeaf = useCallback((props: any) => <Leaf {...props} />, [])
  const editor = useMemo(() => withReact(createEditor()), [])
  return (
    <Slate editor={editor} value={initialValue}>
      <Paper sx={{ flexGrow: 1, justifyContent: "center" }}>
        <Box padding={1}>
          <ToolButton format="bold" icon={<BoldIcon/>}/>
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <ItalicIcon />
          </IconButton>
          
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <UnderlinedIcon />
          </IconButton>
          
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <QuoteIcon />
          </IconButton>
        
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <ListNumberedIcon />
          </IconButton>
          
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <ListBulletedIcon />
          </IconButton>
        
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <AlignLeftIcon />
          </IconButton>
        
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <FormatAlignCenterIcon />
          </IconButton>
          
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <FormatAlignRightIcon />
          </IconButton>
        
          <IconButton size="small" edge="start" sx={{ mr: 2 }}>
            <FormatAlignJustifyIcon />
          </IconButton>
          </Box>
        <Divider/>
        <Box p={3}>
          <Editable
          renderElement={renderElement}
          renderLeaf={renderLeaf}
          placeholder="Enter some rich text"
          spellCheck
          autoFocus
          onKeyDown={event => {
            for (const hotkey in HOTKEYS) {
              if (isHotkey(hotkey, event as any)) {
                event.preventDefault()
                const mark = HOTKEYS[hotkey]
                toggleMark(editor, mark)
              }
            }
          }}
        />
        </Box>
        
      </Paper>
        
      
    </Slate>
  )
}

export default Editor

const initialValue = [
  {
    type: 'paragraph',
    text: ''
  },
]